<script>
import Footer from '../components/Footer';
import ScrollDown from '../components/ScrollDown'
export default {
  name: 'about',
  data: () =>{
    return {
      arrayOfGendenConsoleItems: [
        "awesome", "stunning ", "scalable ", "innovative ", "intuitive ", "awesome ", "addictive "
      ]
    }
 },
  mounted () {
    this.initGendenConsoleTyping("#genden-console-text", this.arrayOfGendenConsoleItems);
  },
  components: {
    'app-footer': Footer,
    'app-scroll-down': ScrollDown
  },
  methods: {
    initGendenConsoleTyping: (selector, arrayOfBrandNames) => {
      if (!$(selector).length) {
        return false;
      }
      var typed = new Typed(selector, {
        strings: arrayOfBrandNames,
        smartBackspace: false, // must remain false, else icons wont animate
        typeSpeed: 60,
        backSpeed: 20,
        contentType: 'html',
        backDelay: 1500,
        loop: true
      });
    }
  }
};
</script>
